}

.column2 {
  flex: 33.3% 1;
  max-width: 33.3%;
  padding: 0 10px;
}

.column3 {
  flex: 33.3% 1;
  max-width: 33.3%;
  padding: 0 10px;
}

.column1 img {
  border-radius: 20px;
